"use client";

import { Button } from "@/components/ui/button";
import { CardFooter } from "@/components/ui/card";
import { Loader2 } from "lucide-react";


export function StepNavigation({
  currentStep,
  isFirstStep,
  isLastStep,
  isValid,
  isSubmitting,
  onPrevious,
  onNext,
  contract
}) {
  return (
    <CardFooter className="flex justify-between bg-muted/30 border-t border-border/50 p-4">
      {!isFirstStep && (
        <Button
          type="button"
          variant="outline"
          onClick={onPrevious}
          disabled={isSubmitting}
          className="min-w-[100px]"
        >
          Previous
        </Button>
      )}
      {isFirstStep && <div />} {/* Spacer for flex justify-between */}
      
      {isLastStep ? (
        <Button
          type="submit"
          disabled={!isValid || isSubmitting || !contract}
          className="min-w-[150px]"
        >
          {isSubmitting ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              Creating...
            </>
          ) : "Create Campaign"}
        </Button>
      ) : (
        <Button
          type="button"
          onClick={onNext}
          disabled={!isValid}
          className="min-w-[100px]"
        >
          Next Step
        </Button>
      )}
    </CardFooter>
  );
}